@font-face {
    font-family: "RobotoBlack";
    font-style: normal;
    font-weight: normal;
    src: url("../fonts/RobotoBlack/RobotoBlack.eot?#iefix") format("embedded-opentype"), url("../fonts/RobotoBlack/RobotoBlack.woff") format("woff"), url("../fonts/RobotoBlack/RobotoBlack.ttf") format("truetype"); }
  
  @font-face {
    font-family: "RobotoLight";
    font-style: normal;
    font-weight: normal;
    src: url("../fonts/RobotoLight/RobotoLight.eot?#iefix") format("embedded-opentype"), url("../fonts/RobotoLight/RobotoLight.woff") format("woff"), url("../fonts/RobotoLight/RobotoLight.ttf") format("truetype"); }
  
  @font-face {
    font-family: "ClearSans-Bold";
    font-style: normal;
    font-weight: normal;
    src:url("../fonts/ClearSans-Bold/ClearSans-Bold.ttf") format("truetype"); }
  
  @font-face {
    font-family: "FontAwesome";
    font-style: normal;
    font-weight: normal;
    src: url("../fonts/FontAwesome/FontAwesome.ttf") format("truetype"); }
    



blockquote,body,code,dd,div,dl,dt,fieldset,form,h1,h2,h3,h4,h5,h6,input,li,p,pre,td,textarea,th,ul{margin:0;padding:0}table{border-collapse:collapse;border-spacing:0}abbr,fieldset,img{border:0}address,caption,cite,code,dfn,em,strong,th,var{font-style:normal;font-weight:400}ul li{list-style:none}caption,th{text-align:left}h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:400}sup{vertical-align:text-top}sub{vertical-align:text-bottom}input,select,textarea{font-family:inherit;font-size:inherit;font-weight:inherit}legend{color:#000}article,aside,details,figcaption,figure,footer,header,hgroup,main,menu,nav,section{display:block}img{max-width:100%;height:auto}.h2sub{text-align:center;font-size:20px;margin:20px 0}


body{
    background-color: #efefe2;
}
.visually-hidden{
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
}
.main-navigation{
    padding: 18px 42px 0 98px;
    display: flex;
    align-items: baseline;
    flex-wrap: wrap;
}
.main-header-logo{
    flex-basis: 139px;
    min-width: 139px;
    font-weight: bold;
    line-height: 41px;
    max-width: 300px;
    flex-grow: 2;
    text-decoration: none;
    color: #000;
    font-family: "ClearSans-Bold", serif;
    img{
        vertical-align: middle;
        margin-right: 8px;
    }
}
.wrap{
    margin-right: 10px;
    flex-basis: 410px;
    min-width: 110px;
    .site-navigation{
        li{
            display: inline-block;
            padding: 0 8px;
            a{
                display: block;
                text-decoration: none;
                color: #000;
                font-size: 15px;
                font-weight: bold;
                font-family: "ClearSans-Bold", serif;
                &:hover{
                    color: #ee5575;
                }
            }
            &.active{
                a{
                    color: #ee5575;
                    position: relative;
                    &::after{
                        content: "";
                        position: absolute;
                        top: 44px;
                        left: 0;
                        right: 0;
                        height: 6px;
                        background: #ee5575;
                        opacity: .2 ;
                    }
                }
            }
            &.nav-toggle{
                position: relative;
                cursor: pointer;
                a::after{
                    font-family: "Font Awesome 5 Free"; 
                    font-weight: 900; 
                    content: "\f107";
                    padding-left: 6px;
                }
                ul{
                    display: none;
                    position: absolute;
                    left: 0;
                    top: 30px;
                    width: 200px;
                    li{
                        
                    }
                }
            }
        }
    }
}
.user-navigation{
    text-align: right;
    display: block;   
    flex-grow: 2;
    min-width: 82px;
}
.btn{
    text-decoration: none;
    box-sizing: border-box;
    border: 1px solid #2f3a3d;
    font-weight: bold;
    font-family: "ClearSans-Bold", serif;
    &:hover{
        cursor: pointer;
        &.order-link{
            background: transparent;
            color: #000;
            
        }
        &.login-link{
            background: #2f3a3d;
            color: white;
        
        }

    }
}
.order-link{
    background: #2f3a3d;
    color: white;
    padding: 2px 18px 6px 19px;
    display: inline-block;
    min-width: 119px;
    vertical-align: middle;
}
.login-link{
    background: transparent;
    padding: 2px 20px 7px 17px;
    color: #000;
    min-width: 102px;
    &::before{
        font-family: "Font Awesome 5 Free"; 
        font-weight: 900; 
        content: "\f023";
        font-size: 12px;
        padding-right: 12px;
    }
    
}
.container{
    padding: 170px 0 226px 0;
}
.main-content{
    img{
        float: left;
        margin-right: 62px;
    }
    h2{
        display: inline-block;
        vertical-align: top;
        font-size: 62px;
        font-family: "RobotoBlack", serif;
        color: #d0ca9e;
        margin-bottom: 29px;
        margin-top: -7px;
        width: 61%;
    }
    .info-item{
        display: flex;
        flex-wrap: wrap;
        position: relative;
        li{
            width: 209px;
            display: inline-block;
            margin-right: 70px;
            font-size: 16px;
            font-family: "RobotoLight", serif;
            .item-title{
                font-size: 26px;
                font-weight: bold;
                margin-bottom: 30px;
                height: 83px;
                font-family: "RobotoBlack", serif;
            }
        }
    }
}
.menuToggle{
    display: none;
}
.serv-hidden{
    background-color: #ee5575;
    border-radius: 8px;
    color: white;
    width: 200px;
}
@media only screen and (max-width : 1200px){
    .main-content{
        text-align: center;
        h2{
            width: 80%;
            padding: 0 10px;
        }
        img{
            float: unset;
            margin-bottom: 10px;
            margin-right: 0;
        }
        .info-item{
            justify-content: space-around;
            li{
                margin: 0 10px;
                width: 280px;
            }
        }
    }
}
@media only screen and (max-width : 960px) {
    .animate{
        display: none;
    }
    .wrap .site-navigation li.nav-toggle ul{
        left: -94px;
    }
    .main-header-logo{
        flex-basis: unset;
        max-width: unset;
        display: block;
        width: 100%;
        text-align: center;
        margin-bottom: 20px!important;
    }
    .wrap{
        display: block;
        width: 100%;
        max-width: unset;
        flex-basis: unset;
        text-align: center;
        margin-bottom: 50px;
    }
    .user-navigation{
        flex-grow: 1;
        text-align: left;
        margin-left: 5px; 
    }
    .order-button{
        flex-grow: 1;
        text-align: right;
        margin-right: 5px; 
    }
    .container {
        padding: 60px 0 226px;
    }
}
@media only screen and (max-width : 574px){
    .main-navigation{
        padding: 18px 20px 0 20px;
    }
    .main-content {
        h2{
            font-size: 48px;
        }
        .info-item li .item-title{
            font-size: 22px;
        }
    }
    .main-content {
        img{
            margin-right: 0;
        }
        .info-item{
            display: block;
            li{
                box-sizing: border-box;
                padding: 0 40px;
                width: 90%;
                margin-bottom: 30px;
            }
            .item-title{
                height: auto!important;
                margin-bottom: 10px;
            }
        }
    }
}
@media only screen and (max-width : 480px){
    .wrap{
        margin: 0 auto 20px;
    }
    .menuToggle{
        display: block;
        margin: auto;
        cursor: pointer;
        border: none;
        background-color: transparent;
        font-size: 26px;
        &:focus{
            color: #ee5575;
            outline-color: #ee5575;
        }
    }
    .site-navigation{
        display: none;
        margin-bottom: 20px;
        margin-top: 10px;
    }
    .wrap .site-navigation li .serv-hidden{
        padding: 0 8px;
        border-bottom: 1px solid white;
        padding-bottom: 5px;
    }
    .wrap .site-navigation > li{
        display: block;
        padding: 10px 8px;
        &.active{
            a{
                &::after{
                    top: 22px;
                    height: 3px;
                    background: #ee5575;
                    opacity: .2 ;
                }
            }
        }
        a{
            display: inline-block;
            padding: 0px 30px;
        }
    }
    .wrap .site-navigation li.nav-toggle ul{
        left: 25%;
        top: 34px;
    }
    .main-content{
        min-width: unset!important;
    }
    .main-content h2{
        font-size: 30px;
        padding: 0 10px;
    }

}
.animate{
    position: absolute;
    width: 100%;
    top: 48.2%;
    opacity: 0;
    transition: .4s ease-in;
    transition-delay: 0.5s;
    -webkit-transition: .4s ease-out;
    -webkit-transition-delay: 0.5s;
    -o-transition: .4s ease-out;
    -o-transition-delay: 0.5s;
    -moz-transition: .4s ease-out;
    -moz-transition-delay: 0.5s;
    ul{ 
        li{
            border-bottom: 1px solid #2f3a3d;
            padding: 14px;
        }
    }
    &.visible{
        opacity: .4;
    }
}

